import './utp.css';
import backgroundImage from './../../img/gradient-background.svg';

const Utp = () => {
	return (
		<section className="utp">
			<div className="utp__background">
				<img src={backgroundImage} alt="image"/>
			</div>
			<div className="container">
				<h2 className="reused-title utp__title">Pusk  ,<br />   </h2>
				<span className="reused-signature" id="muted">     ,    ,       </span>
			</div>
		</section>
	);
}

export default Utp;