import './info.css';

const Info = ({card}) => {
	return (
		<section className="info">
			<div className="container">
				<span className="info__subtitle"> </span>
				<div className="info__wrapper">
					<h3 className="info__title"> </h3>
					<p className="info__paragraph" id="muted"> it ,     {card.title}.  ,       //...     Fintech/Blockchain      .       !    ,    ,  IT .**</p>
					<h3 className="info__title"></h3>
					<p className="info__paragraph" id="muted">{card.tasks}</p>
					<h3 className="info__title"> </h3>
					<p className="info__paragraph" id="muted">{card.advantage}</p>
					<h3 className="info__title"></h3>
					<ul className="info__list">
						<li className="info__offer" id="muted">    (   ).</li>
						<li className="info__offer" id="muted">     (.  /),   ,    .      .</li>
						<li className="info__offer" id="muted">  ( ---, , , ,  , ,      ).</li>
						<li className="info__offer" id="muted">     ,   - ( 100%).</li>
					</ul>
					<br />
					<span id="muted">             !</span>
				</div>
			</div>
		</section>
	);
}

export default Info;